document.addEventListener("DOMContentLoaded", () => {
    const formMembro = document.getElementById('form-membro');
    const membrosList = document.getElementById('membros-list');

    // FunÃ§Ã£o para carregar membros da API
    const loadMembros = async () => {
        try {
            const response = await fetch('http://localhost:3000/api/membros');
            const membros = await response.json();
            membrosList.innerHTML = '';

            membros.forEach(membro => {
                const row = document.createElement('tr');
                row.innerHTML = `
                    <td>${membro.nome}</td>
                    <td>${membro.dataNascimento}</td>
                    <td>${membro.numeroRol}</td>
                    <td>${membro.estadoCivil}</td>
                    <td>${membro.naturalidade}</td>
                    <td>${membro.situacao}</td>
                `;
                membrosList.appendChild(row);
            });
        } catch (error) {
            console.error('Erro ao carregar membros:', error);
        }
    };

    // FunÃ§Ã£o para adicionar um novo membro
    formMembro.addEventListener('submit', async (e) => {
        e.preventDefault();

        const nome = document.getElementById('nome').value;
        const dataNascimento = document.getElementById('dataNascimento').value;
        const numeroRol = document.getElementById('numeroRol').value;
        const estadoCivil = document.getElementById('estadoCivil').value;
        const naturalidade = document.getElementById('naturalidade').value;
        const situacao = document.getElementById('situacao').value;

        const novoMembro = {
            nome,
            dataNascimento,
            numeroRol,
            estadoCivil,
            naturalidade,
            situacao
        };

        try {
            const response = await fetch('http://localhost:3000/api/membros', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(novoMembro)
            });

            if (response.ok) {
                alert('Membro adicionado com sucesso!');
                loadMembros(); // Recarregar lista de membros apÃ³s adicionar
                formMembro.reset(); // Limpar o formulÃ¡rio
            } else {
                alert('Erro ao adicionar membro.');
            }
        } catch (error) {
            console.error('Erro ao adicionar membro:', error);
            alert('Erro ao adicionar membro.');
        }
    });

    // Carregar membros ao carregar a pÃ¡gina
    loadMembros();
});
